@media all and (min-width:1400px){
    .siteContainer{
        width:100% !important;
        height:94.5vh !important;
    }
    nav{
        width:40% !important;
        height:5% !important;
    }
    .messageContainer{
        width:40% !important;
        height:60% !important;
    }
    .chatAndTools{
        width:40% !important;
        height:5% !important;
    } 
    .dhButton{
        height:30px !important;
        width:30px !important;
    }
    .dhImg{
        height:1.6vw !important;
    }
    .siButton{
        height:30px !important;
        width:30px !important;
    }
    .siImg{
        height:1.6vw !important;
    }
}
@media all and (max-width:960px){
    .siteContainer{
        width:100% !important;
        height:94.5vh !important;
    }
    nav{
        width:95% !important;
        height:5% !important;
    }
    .messageContainer{
        width:95% !important;
        height:60% !important;
    }
    .chatAndTools{
        width:95% !important;
        height:5% !important;
    } 
    .chatBox{
        padding:1% !important;
        width:68% !important;
        margin-left:auto !important;
        margin-right:1% !important;
    }
    .enterButton{
        width:18px !important; 
        height:18px !important;
    }
    .dhButton{
        height:4vw !important;
        width:4vw !important;
    }
    .dhImg{
        height:4vw !important;
    }
    .siButton{
        height:4vw !important;
        width:4vw !important;
    }
    .siImg{
        height:4vw !important;
    }
}
.siteHeader{
    display:flex;
    justify-content:center;
    align-items:center;
    height:4vh;
    background-color: rgb(15, 32, 53);
    color:white;
}
.hiddenDelete{
    margin-left:1%;
    width:fit-content;
    transition:all 0.3s ease-in-out;
}
.hiddenDelete:hover{
    transform: scale(1.2);
}
.adminDeleteIcon{
    height:2.5vh;
}
.headerTitle{
    margin-left:auto;
    margin-right:auto;
}
.firebaseLink{
    color:white;
    text-decoration:none;
    transition:all 0.5s ease-in-out;
}
.firebaseLink:hover{
    color:rgb(119, 38, 196);
}
.githubLink{
    margin-right:1%;
    transition:all 0.3s ease-in-out;
}
.githubLink:hover{
    transform: scale(1.2);
}
.ghImg{
    height:2.5vh;
}
.siteContainer{
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    width:100%;
    height:94.5vh;
    background-color: rgb(30, 60, 97);
}
nav{
    display:flex;
    justify-content:center;
    align-items:center;
    width:75%;
    height:5%;
    background-color: rgb(16, 36, 61);
    border-top-style: solid;
    border-bottom-style: solid;
    border-left-style: solid;
    border-right-style: solid;
    border-color:rgb(29, 29, 29);
    border-bottom-color:rgb(35, 61, 92);
    border-top-left-radius:20px;
    border-top-right-radius:20px;
    color:white;
    font-size:1.5rem;
}
.messageContainer{
    display:flex;
    justify-content:center;
    align-items:center;
    width:75%;
    height:60%;
    background-color: rgb(16, 36, 61);
    border-left-style: solid;
    border-right-style: solid;
    border-color:rgb(29, 29, 29);
}
.messageBox{
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
    width:70%;
    height:96%;
    background-color: rgb(35, 61, 92);
    border-radius:20px;
    margin-left:auto;
    margin-right:1%;
}
.generatedHistoryBox{
    display:flex;
    flex-direction:column-reverse;
    width: 90%;
    height:30%;
    margin-left:auto;
    margin-right:3%;
    margin-top:4%;
    margin-bottom:2%;
    overflow-y:scroll;
    border-radius:8px;
    border-style:solid;
    border-color: rgb(67, 102, 148);
    background-color: rgb(34, 56, 83);
}
.hMsgContentHolder{
    display:inline-flex;
    flex-direction:column;
}
.generatedHistMessage{
    display:flex;
    width:fit-content;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    color:white;
    margin-top:2%;
    margin-bottom:2%;
    margin-left:auto;
    margin-right:2%;
    background-color:rgb(27, 47, 71);
    border-radius:10px;
    border-style:solid;
    border-width:2px;
    border-color: rgb(18, 34, 53);
    font-size:0.9rem;
    padding:2%;
    text-align:center;
    transition:all 0.5s ease-in-out;
}
.hMsgContent{
    background-color: rgb(38, 65, 99);
    border-radius:8px;
    width:90%;
    padding:5%;
    word-wrap:normal;
}
.chatRoomTitle{
    margin-left:auto;
    margin-right:auto;
    margin-bottom:auto;
    padding-bottom:2%;
    color:white;
    position:initial;
}
.searchDashBoard{
    display:flex;
    justify-content:center;
    align-items:center;
    width:25%;
    height:96%;
    background-color: rgb(35, 61, 92);
    border-top-right-radius:20px;
    border-bottom-right-radius:20px;
    margin-left:auto;
    margin-right:auto;
}
.searchContainer{
    display:flex;
    flex-direction:column;
    justify-content:space-around;
    height: 98%;
    width: 85%;
}
.searchBarContainer{
    display:flex;
    background-color: rgb(32, 54, 82);
    border-radius:10px;
    height:3.5%;
}
.searchInput{
    width:80%;
    border-style:none;
    background-color: rgb(27, 47, 71);
    outline:none;
    color:white;
    padding-left:4%;
}
.resultsContainer{
    display:flex;
    justify-content:center;
    align-items:center;
    background-color: rgb(27, 47, 71);
    height:90%;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    color:white;
}
.generatedUserContainer{
    display:flex;
    height:90%;
    justify-content:center;
    justify-content:space-around;
    align-items: center;
}
.userColumn{
    display:flex;
    flex-direction: column;
    justify-content:center;
    justify-content:space-around;
    align-items:center;
    height:100%;
}
.photoColumn{
    display:flex;
    flex-direction:column;
    justify-content:center;
    justify-content:space-around;
    align-items:center;
    height:100%;
    padding-right:10%;
}
.guImgHolder{
    display:flex;
    justify-content:center;
    align-items:center;
    height:2.5vh;
    padding:15%;
}
.generatedUser{
    display:flex;
    width:fit-content;
    height:2.5vh;
    text-align: center;
    font-size:0.8rem;
    background-color: rgb(45, 74, 112);
    padding:8%;
    border-bottom-right-radius:8px;
    border-top-right-radius: 8px;
}
.guImg{
    border-style:solid;
    border-width: 2px;
    height:2.5vh;
    border-radius:16px;
    border-color: rgb(67, 102, 148);
    padding:8%;
    transition:all 0.5s ease-in-out;
}
.guImg:hover{
    border-color:rgb(2, 211, 141)
}
.chatAndTools{
    width:75%;
    display:flex;
    justify-content:center;
    align-items: center;
    background-color: rgb(16, 36, 61);
    box-shadow: 0px 10px 5px rgb(20, 43, 71);
    border-top-style: solid;
    border-bottom-style: solid;
    border-left-style: solid;
    border-right-style: solid;
    border-color:rgb(29, 29, 29);
    border-top-color:rgb(35, 61, 92);
    border-bottom-left-radius:20px;
    border-bottom-right-radius:20px;
    padding-top:1%;
    padding-bottom:1%;
}
.chatBox{
    display:flex;  
    background-color: azure;
    width: 69%;
    height:3vh;
    padding:0.5%;
    border-radius:10px;
    text-decoration:none;
    border:none;
    margin-left:auto;
    margin-right:1%;
}
.enterButton{
    display:flex;
    justify-content:center;
    align-items:center;
    height:100%;
    width:fit-content;
    border-radius:5px;
    border-style:none;
    background-color:azure;
    margin-left:auto;
    transition:all 0.3s ease-in-out;
    margin-top:auto;
    margin-bottom:auto;
}
.enterButton:hover{
    background-color:rgb(197, 209, 209);
}
.sendImg{
    align-self:center;
    height:100%;
}
.inputForm{
    display:flex;
    width:100%;
}
.chatInput{
    width:85%;
    border-radius:20px;
    border-style:none;
    outline:none;
}
.shortcutOptions{
    display:flex;
    justify-content:space-between;
    width:25%;
    margin-left:auto;
    margin-right:auto;
}
.deleteHistory{
    display:flex;
}
.dhButton{
    display:flex;
    justify-content:center;
    align-items:center;
    height:3vw;
    width:3vw;
    transition:all 0.3s ease-in-out;
    background-color: rgb(16, 36, 61);
    border:none;
}
.dhImg{
    margin-left:auto;
    margin-right:auto;
    height:3vw;
}
.dhButton:hover{
    transform:scale(1.2);
}
.signIn{
    display:flex;
}
.siButton{
    display:flex;
    justify-content:center;
    align-items:center;
    height:3vw;
    width:3vw;
    border-style:none;
    transition:all 0.3s ease-in-out;
    background-color: rgb(16, 36, 61);
}
.siImg{
    margin-left:auto;
    margin-right:auto;
    height:3vw;
}
.siButton:hover{
    transform:scale(1.2);
}
.generatedMessageHolder{
    display:flex;
    height:55%;
    width:90%;
    margin-left:auto;
    margin-right:3%;
    margin-bottom:2%;
    border-radius:8px;
    border-style:solid;
    border-color: rgb(67, 102, 148);
    background-color: rgb(34, 56, 83);
}
.generatedMessageBox{
    display:flex;
    flex-direction:column;
    width: 90%;
    max-height:85%;
    margin-top:auto;
    margin-left:auto;
    margin-right:3%;
    margin-bottom:2%;
    overflow-y:auto;
    overflow-x:hidden;
    background-color: rgb(34, 56, 83);
}
.msgContentHolder{
    display:inline-flex;
    flex-direction:column;
}
.generatedMessage{
    display:flex;
    width:fit-content;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    color:white;
    margin-top:2%;
    margin-bottom:2%;
    margin-left:auto;
    margin-right:2%;
    background-color:rgb(27, 47, 71);
    border-radius:10px;
    border-style:solid;
    border-width:2px;
    border-color: rgb(18, 34, 53);
    font-size:0.9rem;
    padding-top:2%;
    padding-left:1%;
    padding-right:1%;
    text-align:center;
    transition:all 0.5s ease-in-out;
}
.msgContent{
    background-color: rgb(38, 65, 99);
    border-radius:8px;
    width:94%;
    max-width: 400px;
    margin-left:auto;
    margin-right:auto;
    padding:2%;
    word-wrap: break-word;
}
.msgBoxName{
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:0.8rem;
    opacity:0.5;
    margin-top:5px;
    margin-bottom:5px;
    width:90%;
    height:2rem;
    margin-left:auto;
    margin-right:auto;
    transition:all 0.5s ease-in-out;
}
.msgBoxName:hover{
    opacity:1;
}
.userIMG{
    margin-left:2%;
    height:80%;
    border-radius:20px;
}
.loginInfo{
    display:flex;
    justify-content:center;
    align-items:center;
    margin-top: 2%;
    color:rgb(214, 211, 211);
}
.generatedMessageBox::-webkit-scrollbar{
    background-color: rgb(35, 61, 92);
    width:10px;
}
.generatedMessageBox::-webkit-scrollbar-thumb{
    background-color: rgb(27, 47, 71);
}
.generatedHistoryBox::-webkit-scrollbar{
    background-color: rgb(35, 61, 92);
    width:10px;
}
.generatedHistoryBox::-webkit-scrollbar-thumb{
    background-color: rgb(27, 47, 71);
}
.nameHolder{
    background-color: blueviolet;
}